// -new
export const newAddShoppingCartAdd = (params) => {
	console.log("API:", params);
	return request({
		url: '/user/shoppingCart/add',
		method: 'POST',
		params: JSON.stringify(params), // 
		header: {
			'Content-Type': 'application/json; charset=utf-8', // 
			'token': uni.getStorageSync('token')
		}
	})
}

